import Vue from "vue";
import Vuex from "vuex"
import mutations from "./mutations";
import actions from "./actions";
Vue.use(Vuex);
const  store = new Vuex.Store({

  state:{
    topiclist:[
      { name:"all",desc:"全部"},
      { name:"good",desc:"精华"},
      { name:"share",desc:"分享"},
      { name:"ask",desc:"问答"},
      { name:"job",desc:"招聘",},
      { name:"dev",desc:"测试",},
    ],
    issCon:false,   //默认不显示actionsheet弹出框
    isLogined:false,  //默认为未登录状态
    loginedMenus: {
      create:"发布新话题",
      center:"个人中心",
      msgs:"消息中心",
      logout:"退出",
    },
    nulogunMenus:{
      login:"登陆"
    },
    loginname:"",  //保存登陆后的用户名
    accesstoken:"", //保存登陆后的访问令牌
    author_id:"",  //保存登陆用户的id
    msgsCount:"",  //未读消息的数量

  },
  getters:{
    moreMenus(state){    //根据1登陆状态显示不同1的菜单
      if (state.isLogined){    //判断用户是否登陆
        return state.loginedMenus   //登陆后显示登陆后的菜单
      }
      return state.nulogunMenus    //没有登陆后显示没有登陆的菜单
    },
  //  处理分类显示汉字
    fileterIab(state){
        return function (tab) {
          for (var i = 0; i <state.topiclist.length ; i++) {
              const element = state.topiclist[i]
            if (element.name==tab){
                return state.topiclist[i].desc
            }
          }
        }
    }
  },
  mutations,
  actions,



});

export  default store;



