
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
name: "Create",

data(){
return{
title:"",  //发表主题的标题
content:"",  //发表主题的内容

toolbars: {
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
strikethrough: true, // 中划线
quote: true, // 引用
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
table: true, // 表格
fullscreen: true, // 全屏编辑
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 1.3.5 */
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
preview: true, // 预览
}
}

},
components:{
XTextarea,
Group,
XButton,
mavonEditor
},
methods:{
hantopic:function () {
var app=this
console.log("aaaaa")
this.$http.post("/topics",{
accesstoken:this.$store.state.accesstoken,  //访问令牌
tab:"dev",  //帖子的专区
title:this.title,  //标题
content:this.content 
}).then(function (res) {
console.log(res)
if (res.data.success){   //发表成功后跳转到指定板块
app.$router.push("/topiclist/dev")
}
})
}

}
}



