const  mutations={
  mutationsIsShow:function (state,payload) {
    state.issCon =payload    //修改issCon的值使之弹出弹出框
  },
//  修改登陆后的状态
  mutationsLoined:function (state) {
    state.isLogined = true;
  },
//  保存登陆后的用户名和令牌
  mutationsLonginnema:function (state,name) {
      state.loginname =name
  },
  mutationsAccess:function (state,name) {
  state.accesstoken=name
  },

//  退出后清空令牌和用户名
  mutationsUserinof:function (state) {
    state.isLogined = false;
    state.loginname="";
    state.accesstoken=""
  },
//  保存用户id进state
  mutationsAutId:function (state,id) {
    state.author_id = id
  },
  //修改未读消息
  mutationsmsgs:function (state,msgsCount) {
    state.msgsCount=msgsCount
  }

};
export default mutations



